import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      meta: {
        title: '广东电信省NOC数据可视化平台'
      },
      component: resolve => require(['../components/Container.vue'], resolve),
      children: [
        {
          path: '/User-defined',
          component: resolve => require(['../components/User-defined.vue'], resolve)
        },
        {
          path: '/weekly',
          component: resolve => require(['../components/Weekly.vue'], resolve)
        },
        {
          path: '/monthly',
          component: resolve => require(['../components/Monthly.vue'], resolve)
        },
        {
          path: '/quarterly',
          component: resolve => require(['../components/Quarterly.vue'], resolve)
        }
        // {
        //   path: '/yearly',
        //   component: resolve => require(['../components/Yearly.vue'], resolve)
        // },
        // {
        //   path: '/professional-analysis',
        //   component: resolve => require(['../components/ProfessionalAnalysis.vue'], resolve)
        // }
      ],
      redirect: '/User-defined'
    }
  ]
})



